import Header from '@/components/Header.vue';
import { useStore } from 'vuex';
import { useRoute, useRouter } from 'vue-router';
import { reactive, toRefs, onMounted, computed } from 'vue';
import { showToast } from 'vant';
export default {
  components: {
    Header
  },
  setup() {
    // useRoute
    // useRouterrouter()
    const route = useRoute();
    const router = useRouter();
    const store = useStore();
    const data = reactive({
      // 
      areaList: {
        province_list: {
          110000: '',
          120000: '',
          130000: ''
        },
        city_list: {
          110100: '',
          110200: '',
          120100: '',
          120200: '',
          130100: ''
        },
        county_list: {
          110101: '',
          110102: '',
          120102: '',
          130102: '',
          130101: ''
        }
      },
      //
      addressInfo: []
    });
    const handleTitle = computed(() => {
      const title = route.query.type === 'add' ? '' : '';
      return title;
    });
    const onSave = content => {
      if (route.query.type === 'add') {
        // idaddAddressuserAddress
        content.id = `100${store.state.userAddress.length + 1}`;
        store.commit('addAddress', content);
        console.log('add Address info', content);
        router.back();
      } else {
        store.commit('editAddress', content);
        console.log('edit Address info', content);
        // router.push('/address')
        router.back();
      }
      showToast(`${route.query.id === 'add' ? '' : ''}`);
    };
    // 13484198017
    const onDelete = content => {
      store.commit('deleteaddress', content);
      showToast('Delete successsful ');
      router.back();
    };
    const init = () => {
      store.state.userAddress.forEach(item => {
        // address,,id
        // addressInfo
        if (item.id === Number(route.query.id)) {
          data.addressInfo = item;
        }
      });
    };
    onMounted(() => {
      init();
    });
    return {
      router,
      route,
      handleTitle,
      ...toRefs(data),
      onSave,
      onDelete
    };
  }
};